import { Component, OnInit, OnChanges} from '@angular/core';

@Component({
  selector: 'app-publication',
  templateUrl: './publication.component.html',
  styleUrls: ['./publication.component.css']
})
export class PublicationComponent implements OnChanges {

   text: string;
  fullText = true;
  showMore = false;
  showLess = false;
  rmTextShort = ''; 
  rmTextFull = ''; 
  inputWords = [];
  

  readMore(flag) {
      if (flag) {
          this.showMore = false;
          this.fullText = true;
          this.rmTextFull = this.text;
          this.showLess = true;
      } else {
          this.showLess = false;
          this.showMore = true;
          this.fullText = false;
      }
  }

  ngOnChanges () {
      this.rmTextShort = this.text;
      this.rmTextFull = this.text;
      this.inputWords = this.text.split(' ');
      if (this.inputWords.length > 30) {
          this.fullText = false;
          this.showMore = true;
          this.rmTextShort = this.inputWords.slice(0, 30).join(' ') + '...';
      } else {
          if (this.rmTextShort.length > 300) {
              this.fullText = false;
              this.showMore = true;
              this.rmTextShort = this.rmTextShort.substr(0, 300) + '...'
          } else {
              const lineBreaks = this.rmTextShort.split(/\n/g)
              if (lineBreaks.length > 4) {
                  this.fullText = false
                  this.showMore = true
                  this.rmTextShort = lineBreaks.slice(0, 4).join('\n') + '...'
              }
          }
      }
  }

  constructor() { }

  ngOnInit() {
  }

}
